@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

@import "./components/header";
@import "./components/hero";
@import "./components/production";
@import "./components/projects";
@import "./components/callback";
@import "./components/company";
@import "./components/partners";
@import "./components/expert";
@import "./components/seo-block";
@import "./components/faq";
@import "./components/career";

.before-footer {
  background: linear-gradient(132deg, #c1d2f6 0%, #e3e8f3 100%);
  padding-top: 80px;
  margin-top: 120px;
  padding-bottom: 40px;
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
  }

  p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: var(--chernyy);
    margin: 0;
    margin-bottom: 30px;
    margin-top: 8px;
  }

  .footer-form {
    display: flex;
    flex-direction: column;
    gap: 10px;

    &__block {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 10px;
    }
    &__bottom {
      margin-top: 20px;
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 10px;
      .policy {
        display: flex;
        flex-direction: column;
        gap: 8px;
        max-width: 325px;
        width: 100%;
        .policy-block {
          position: relative;
          .checkbox {
            position: absolute;
            opacity: 0;
            appearance: none;
            width: 100%;
            height: 100%;
            cursor: pointer;
            z-index: 1;
          }
          label {
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 12px;
            color: var(--chernyy);
            position: relative;
            display: block;
            padding-left: 24px;
            a {
              text-decoration: underline;
              text-decoration-skip-ink: none;
              color: inherit;
              position: relative;
              z-index: 2;
            }

            &:before {
              content: "";
              position: absolute;
              top: 0;
              left: 0;
              width: 14px;
              height: 14px;
              border-radius: 2px;
              border: 1px solid var(--siniy);
              position: absolute;
              transition: 0.3s ease;
            }
          }

          .checkbox:checked + label:before {
            background: url("../../img/icons/checkbox.svg") no-repeat;
            border-color: transparent;
          }
        }
      }
      .btn--accent {
        border-radius: 8px;
        padding: 12px 40px;
        width: 100%;
      }
    }
  }

  &__image {
    position: absolute;
    bottom: -40px;
    right: 0;
    max-width: 480px;

    img {
      display: block;
    }
  }
}

.footer {
  background: linear-gradient(132deg, #c1d2f6 0%, #e3e8f3 100%);
  padding-block: 40px;
  border-top: 1px solid var(--divayder);

  &__logo {
    display: block;
    margin-bottom: 20px;
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 20px;
  }

  .address {
    display: flex;
    flex-direction: column;
    gap: 14px;

    strong {
      font-family: var(--font-family);
      font-weight: 600;
      font-size: 14px;
      color: var(--chernyy);
    }

    p {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 12px;
      color: var(--chernyy);
      max-width: 290px;
      position: relative;
      padding-left: 18px;
      margin: 0;
      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 14px;
        height: 14px;
        background: url("../img/icons/map.svg") center center no-repeat;
      }
      span {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 12px;
        color: var(--vspomogatenye-elementy);
        display: block;
        margin-top: 5px;
      }
    }
  }

  &__nav {
    display: flex;
    flex-direction: column;
    gap: 8px;

    &-title {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: clamp(14px, 2vw, 18px);
      line-height: 120%;
      text-transform: uppercase;
      color: var(--chernyy);
      padding-inline: 4px;
    }

    &-list {
      display: flex;
      flex-direction: column;
      gap: 4px;

      a,
      span {
        font-family: var(--font-family);
        font-weight: 600;
        font-size: 14px;
        color: var(--chernyy);
        display: flex;
        align-items: center;
        gap: 6px;
        transition: background-color 0.3s ease;
        border-radius: 4px;
        padding: 4px;
        @include hover {
          &:hover {
            background-color: var(--belyy);
          }
        }
      }
    }

    .link-sublist:has(.sublist) {
      cursor: pointer;
      span {
        pointer-events: none;
      }
      .sublist {
        display: none;
        flex-direction: column;
        gap: 2px;
        margin-top: 10px;
        &.vis {
          display: flex;
        }
        a {
          font-weight: 400;
        }
      }
    }

    .link-sublist:has(.vis) {
      span svg {
        rotate: 180deg;
      }
    }
  }

  .footer-menu {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    max-width: 790px;
    width: 100%;
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  &__phone {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 18px;
    line-height: 120%;
    text-transform: uppercase;
    color: var(--chernyy);
    transition: color 0.3s ease;

    @include hover {
      &:hover {
        color: var(--oranzhevyy);
      }
    }
  }

  &__mail {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: var(--chernyy);
    transition: color 0.3s ease;
    @include hover {
      &:hover {
        color: var(--oranzhevyy);
      }
    }
  }

  &__socials {
    display: flex;
    gap: 5px;

    a {
      transition: opacity 0.3s ease;
      will-change: opacity;

      @include hover {
        &:hover {
          opacity: 0.7;
        }
      }
    }
  }

  &__btn {
    border-radius: 8px;
    padding: 12px 40px;
    margin-top: 10px;
  }
}

.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(50px);
  opacity: 0;
  visibility: hidden;
  transition: 0.3s ease;

  &._active {
    opacity: 1;
    visibility: visible;
  }
}
