:root {
  --font-family: "Roboto", sans-serif;
  --second-family: "Inter", sans-serif;
  --third-family: "Gilroy", sans-serif;
  --content-width: 1540px;
  --container-offset: 20px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --chernyy: #383838;
  --siniy: #233589;
  --oranzhevyy: #ed662c;
  --compoglass: #289b6b;
  --hover-golubogo: #e3e8f3;
  --fon: #f2f4f9;
  --fon-seryy: #fafafa;
  --belyy: #fff;
  --divayder: rgba(56, 56, 56, 0.2);
  --vspomogatenye-elementy: rgba(56, 56, 56, 0.5);
  --vspomogatenye-elementy-belyy: rgba(255, 255, 255, 0.5);
  --divayder-belyy: rgba(255, 255, 255, 0.2);
}

