html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 370px;
  min-height: 100%;
  font-size: 16px;
  overflow-x: hidden;
  background-color: var(--belyy);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

@media (max-width: 768px) {
  .hidden-sm {
    display: none !important;
  }
}

.btn--gray {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  border-radius: 8px;
  padding: 8px 20px;
  background: var(--fon);
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  line-height: 130%;
  color: var(--chernyy);
  transition: background-color 0.2s ease, color 0.2s ease;
}
@media (any-hover: hover) {
  .btn--gray:hover:hover {
    background: var(--siniy);
    color: var(--belyy);
  }
  .btn--gray:hover:hover svg path {
    stroke: #fff;
  }
}

.input-search {
  border: 1px solid var(--divayder);
  border-radius: 8px;
  padding: 15px 20px;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  line-height: 130%;
  color: var(--vspomogatenye-elementy);
  outline: none;
  transition: 0.3s ease;
}
.input-search:focus {
  outline: 1px solid var(--siniy);
}

.btn--accent {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  line-height: 130%;
  color: var(--belyy);
  padding: 8px 20px;
  transition: 0.3s ease;
  background: var(--siniy);
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}
@media (any-hover: hover) {
  .btn--accent:hover:hover {
    background: var(--oranzhevyy);
  }
}

.title__section {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 28px;
  text-transform: uppercase;
  color: var(--chernyy);
  margin: 0;
}

.input-form input {
  border-radius: 8px;
  padding: 12px 24px;
  width: 100%;
  background: var(--belyy);
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  line-height: 130%;
  color: var(--vspomogatenye-elementy);
  border: none;
  outline: none;
}
.input-form input:focus {
  outline: 1px solid orange;
}

.aside-filter {
  border-radius: 8px;
  padding: 20px 10px;
  background: var(--fon);
}
.aside-filter .aside-filter-toggler {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 16px;
  line-height: 120%;
  color: var(--chernyy);
  position: relative;
  margin-bottom: 20px;
  cursor: pointer;
}
.aside-filter .aside-filter-toggler:after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  width: 20px;
  height: 16px;
  background: url("../img/icons/arrow-city.svg") no-repeat;
  background-size: contain;
}
.aside-filter .aside-filter-options {
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.aside-filter .aside-filter-options .filter-option {
  border-radius: 8px;
  padding: 8px 14px;
  position: relative;
}
.aside-filter .aside-filter-options .filter-option input {
  opacity: 0;
  appearance: none;
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  margin: 0;
  cursor: pointer;
}
.aside-filter .aside-filter-options .filter-option label {
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
  color: var(--chernyy);
  display: block;
  padding-left: 10px;
  margin-left: 5px;
  transition: 0.3s ease;
}
.aside-filter .aside-filter-options .filter-option label:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  border-radius: 2px;
  border: 1px solid var(--vspomogatenye-elementy);
  width: 16px;
  height: 16px;
  transition: 0.3s ease;
  margin: 0;
}
.aside-filter .aside-filter-options .filter-option input:checked + label:before {
  background: url("../img/icons/checkbox.svg") center/cover no-repeat;
  border: none;
  left: 10px;
}
.aside-filter .aside-filter-options .filter-option input:checked + label {
  padding-left: 20px;
}
.aside-filter .aside-filter-options .filter-option:has(input:checked) {
  background: linear-gradient(132deg, #c1d2f6 0%, #e3e8f3 100%);
}

.banner-section {
  position: relative;
  border-radius: 8px;
  padding: 20px;
  overflow: hidden;
}
.banner-section__bg {
  z-index: -1;
  position: absolute;
  inset: 0;
}
.banner-section__title {
  margin: 0;
  margin-bottom: 40px;
}
.banner-section .list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.banner-section__btn {
  border: 2px solid var(--siniy);
  border-radius: 8px;
  padding: 12px 40px;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 16px;
  line-height: 120%;
  color: var(--chernyy);
  transition: background-color 0.3s ease, color 0.3s ease;
}
.banner-section__btn.active {
  background: var(--siniy);
  color: var(--belyy);
}
@media (any-hover: hover) {
