    background: var(--siniy);
    color: var(--belyy);
  }
}
.banner-section__select {
  position: relative;
  max-width: 375px;
}
.banner-section__select:after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 20px;
  width: 16px;
  height: 12px;
  background: url("../img/icons/arrow-city.svg") center/cover no-repeat;
}
.banner-section__select select {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
