  }
}
.header__mail {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  line-height: 130%;
  color: var(--chernyy);
  transition: color 0.3s ease;
}
@media (any-hover: hover) {
  .header__mail:hover:hover {
    color: var(--oranzhevyy);
  }
}
.header .center {
  padding-block: 13px;
}
.header .center__wrapper {
  display: flex;
  gap: 60px;
  align-items: center;
}
.header .center .logo {
  display: flex;
  align-items: center;
  gap: 10px;
}
.header .center .logo span {
  font-size: 11px;
  font-weight: bold;
  line-height: 110%;
  color: var(--siniy);
  max-width: 77px;
}
.header .center__links {
  display: flex;
  gap: 10px;
  max-width: 785px;
  margin-left: 50px;
}
.header .center__links a {
  font-family: var(--second-family);
  font-weight: 500;
  font-size: 14px;
  text-transform: uppercase;
  text-align: center;
  color: var(--chernyy);
  border: 1px solid var(--compoglass);
  border-radius: 8px;
  padding: 4px 40px;
  transition: 0.3s ease;
}
