}
@media (any-hover: hover) {
  .header .center__links a.orange:hover:hover {
    background: var(--oranzhevyy);
    color: var(--belyy);
  }
}
.header .sublist__wrapper {
  position: absolute;
  top: 60px;
  z-index: 2;
  width: 365px;
  display: none;
}
.header .sublist__wrapper .sublist {
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.header .sublist__wrapper .sublist__link {
  border-radius: 8px;
  padding: 15px 20px;
  background: var(--fon);
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 14px;
  text-transform: uppercase;
  color: var(--chernyy);
  position: relative;
  transition: 0.3s ease;
}
.header .sublist__wrapper .sublist__link:after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 20px;
  width: 8px;
  height: 12px;
  background: url("../../img/icons/arrow-link-menu.svg") no-repeat;
}
@media (any-hover: hover) {
  .header .sublist__wrapper .sublist__link:hover:hover {
    background: linear-gradient(132deg, #c1d2f6 0%, #e3e8f3 100%);
