.header .bottom {
  background: linear-gradient(132deg, #e3e8f3 0%, #c1d2f6 100%);
  padding-block: 15px;
}
.header .bottom__menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}
.header .bottom__menu .nav .list {
  display: flex;
  align-items: center;
}
.header .bottom__menu .nav .list li {
  position: relative;
}
@media (any-hover: hover) {
  .header .bottom__menu .nav .list li:hover:hover:has(.sublist__wrapper) .sublist__wrapper {
    display: block;
  }
}
.header .bottom__menu .nav .list li:has(.sublist__wrapper):after {
