  position: absolute;
  height: 60px;
  width: 100%;
  bottom: -30px;
  left: 0;
}
.header .bottom__menu .nav .list a {
  border-radius: 8px;
  padding: 10px 20px;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 14px;
  text-transform: uppercase;
  color: var(--chernyy);
