.header {
  padding-top: 16px;
  position: relative;
  z-index: 2;
  &__top {
    &-wrapper {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 20px;
    }

    &-left {
      display: flex;
      align-items: center;
      gap: 4px;
    }

    &-right {
      display: flex;
      align-items: center;
      gap: 20px;
    }
  }

  .language {
    border-radius: 4px;
    padding: 8px;
    background: var(--fon);
    display: flex;
    flex-direction: column;
    gap: 3px;
    position: relative;
    button {
      font-family: var(--font-family);
      font-weight: 600;
      font-size: 14px;
      color: var(--chernyy);
      align-items: center;
      gap: 4px;
      transition: 0.3s ease;
      display: flex;
      &:before {
        content: "";
        width: 21px;
        height: 15px;
        background: url("../../img/icons/rus-flag.svg") no-repeat;
      }
    }
  }

  &__city {
    position: relative;
    select {
      background: #000;
      background: var(--fon);
      border-radius: 4px;
      padding: 8px 14px;
      border: none;
      font-family: var(--font-family);
      font-weight: 600;
      font-size: 14px;
      color: var(--chernyy);
      appearance: none;
      width: 120px;
    }

    &:after {
      content: "";
      width: 10px;
      height: 6px;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 10px;
      background: url("../../img/icons/arrow-city.svg") no-repeat;
    }
  }

  &__search {
    max-width: 500px;
    width: 100%;
    position: relative;
    input {
      width: 100%;
    }

    .btn-search {
      position: absolute;
      width: 40px;
      height: 40px;
      display: grid;
      place-items: center;
      background: var(--fon);
      border-radius: 4px;
      top: 5px;
      right: 5px;
      transition: 0.3s ease;

      @include hover {
        &:hover {
          background: var(--siniy);

          svg path {
            fill: #fff;
          }
        }
      }
    }
  }

  &__phone {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: clamp(16px, 1.2vw, 18px);
    line-height: 120%;
    text-transform: uppercase;
    color: var(--chernyy);
    transition: color 0.3s ease;

    @include hover {
      &:hover {
        color: var(--oranzhevyy);
      }
    }
  }

  &__socials {
    display: flex;
    gap: 5px;
    align-items: center;
    a {
      transition: opacity 0.3s ease;
      @include hover {
        &:hover {
          opacity: 0.5;
        }
      }
    }
  }
  &__mail {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: var(--chernyy);
    transition: color 0.3s ease;

    @include hover {
      &:hover {
        color: var(--oranzhevyy);
      }
    }
  }

  .center {
    padding-block: 13px;

    &__wrapper {
      display: flex;
      gap: 60px;
      align-items: center;
    }

    .logo {
      display: flex;
      align-items: center;
      gap: 10px;

      span {
        font-size: 11px;
        font-weight: bold;
        line-height: 110%;
        color: var(--siniy);
        max-width: 77px;
      }
    }

    &__links {
      display: flex;
      gap: 10px;
      max-width: 785px;
      margin-left: 50px;
      a {
        font-family: var(--second-family);
        font-weight: 500;
        font-size: 14px;
        text-transform: uppercase;
        text-align: center;
        color: var(--chernyy);
        border: 1px solid var(--compoglass);
        border-radius: 8px;
        padding: 4px 40px;
        transition: 0.3s ease;
        @include hover {
          &:hover {
            background: var(--compoglass);
            color: var(--belyy);
          }
        }
        &.blue {
          border: 1px solid var(--siniy);
          @include hover {
            &:hover {
              background: var(--siniy);
              color: var(--belyy);
            }
          }
        }
        &.orange {
          border: 1px solid var(--oranzhevyy);
          @include hover {
            &:hover {
              background: var(--oranzhevyy);
              color: var(--belyy);
            }
          }
        }
      }
    }
  }

  .sublist__wrapper {
    position: absolute;
    top: 60px;
    z-index: 2;
    width: 365px;
    display: none;
    .sublist {
      display: flex;
      flex-direction: column;
      gap: 4px;

      &__link {
        border-radius: 8px;
        padding: 15px 20px;
        background: var(--fon);
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 14px;
        text-transform: uppercase;
        color: var(--chernyy);
        position: relative;
        transition: 0.3s ease;
        &:after {
          content: "";
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          right: 20px;
          width: 8px;
          height: 12px;
          background: url("../../img/icons/arrow-link-menu.svg") no-repeat;
        }

        @include hover {
          &:hover {
            background: linear-gradient(132deg, #c1d2f6 0%, #e3e8f3 100%);
          }
        }
      }
    }
  }

  .bottom {
    background: linear-gradient(132deg, #e3e8f3 0%, #c1d2f6 100%);
    padding-block: 15px;

    &__menu {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 20px;
      .nav {
        .list {
          display: flex;
          align-items: center;
          li {
            position: relative;

            @include hover {
              &:hover {
                &:has(.sublist__wrapper) {
                  .sublist__wrapper {
                    display: block;
                  }
                }
              }
            }

            &:has(.sublist__wrapper) {
              &:after {
                content: "";
                position: absolute;
                height: 60px;
                width: 100%;
                bottom: -30px;
                left: 0;
              }
            }
          }
          a {
            border-radius: 8px;
            padding: 10px 20px;
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 14px;
            text-transform: uppercase;
            color: var(--chernyy);
            display: flex;
            align-items: center;
            gap: 8px;
            &.active {
              background: var(--siniy);
              color: var(--belyy);
            }
          }
        }
      }
    }
  }
}
