.production {
  margin-bottom: 60px;
  &__title {
    margin-bottom: 30px;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: 500px 1fr;
    gap: 10px;
  }

  &__content {
    display: flex;
    flex-direction: column;
    box-shadow: 0 4px 13px 0 rgba(30, 55, 91, 0.14);
    background: var(--fon);
    padding: 20px;
    border: 1px solid var(--siniy);
    border-radius: 8px;

    .subtitle {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 18px;
      line-height: 120%;
      text-transform: uppercase;
      color: var(--chernyy);
      margin-bottom: 16px;
    }
    span {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 12px;
      color: var(--siniy);
      display: block;
      margin-bottom: 8px;
    }
    p {
      margin: 0;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 130%;
      color: var(--chernyy);
      margin-bottom: 30px;
    }

    .list {
      display: flex;
      flex-wrap: wrap;
      gap: 8px;
      margin-bottom: 20px;
      li {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 12px;
        color: var(--chernyy);
        box-shadow: 0 4px 13px 0 rgba(30, 55, 91, 0.14);
        background: linear-gradient(132deg, #c1d2f6 0%, #e3e8f3 100%);
        border-radius: 8px;
        padding: 4px 16px;
      }
    }
  }

  .btn--accent {
    max-width: 285px;
    padding: 10px 20px;
  }

  .prodction__images {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;

    .image {
      overflow: hidden;
      border-radius: 8px;
      position: relative;
      img {
        width: 100%;
        height: 100%;
        border-radius: 8px;
      }

      &__title {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: 18px;
        line-height: 120%;
        text-transform: uppercase;
        color: var(--belyy);
        position: absolute;
        bottom: 20px;
        left: 20px;
      }
    }
  }
}
